// dependencies
import { __html, html, __attr, attr } from '@kenzap/k-cloud';

class Layout_Class{

    // init class
    constructor(data){
        
      // cache data
      this.data = data;
    
      // render section
      this.render();
    }

    // render class html
    render = () => {

      document.querySelector('#content').insertAdjacentHTML('beforeend', 
        `
        <section id="${ attr(this.data.id) }" class="kQFW7T ${ this.data.c.classes ? attr(this.data.c.classes) : '' }" style="${ this.data.c.section }">
            <div class="container" style="${ this.data.c.container }">
              ${ this.data.header.value }


          </div>
        </section>
        `
      );
    }
}

window.Layout_Class = Layout_Class;